/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  min-height: 100vh;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.contenedor__main {
  flex: 1;
}

footer {
  bottom: 0;
  width: 100%;
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 20px;
}

/*h1, h2, h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
    color: v.$blanco;
    text-align: center;
}*/
h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.footer {
  width: 100%;
}

header {
  background-color: aliceblue;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 10px 10%;
}

.logo {
  cursor: pointer;
}

.logo img {
  height: 70px;
  width: auto;
  transition: all 0.3s;
}

.logo img:hover {
  transform: scale(1.2);
}

.menu {
  list-style: none;
}

.menu li {
  display: inline-block;
  padding: 0 20px;
  transition: all 0.3s;
}

.menu a {
  font-size: 700;
  color: black;
}

.menu li:hover {
  transform: scale(1.2);
}

.menu a:hover {
  color: green;
}

.activate {
  background-color: green;
  border-radius: 10px;
}

.activate a:hover {
  color: aliceblue;
}

.login-title {
  font-size: 30px;
  font-weight: 700;
  text-align: center;
}

.welcome {
  text-align: center;
}

.productos {
  margin-left: 10%;
  margin-right: 10%;
  display: grid;
  gap: 20px;
  justify-items: center;
  align-items: center;
  height: 100%;
  /* establece la altura en 100% */
}
@media (min-width: 768px) {
  .productos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}
.productos .producto img {
  width: 100%;
  height: 60%;
  object-fit: cover;
}
.productos .botones {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.productos .boton-carrito {
  padding: 10px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}
.productos .boton-agregar {
  padding: 10px;
  background-color: #fff;
  color: #333;
  border: 2px solid #333;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.calificacion {
  margin-bottom: 10px;
  font-size: 24px;
}

.estrella {
  color: gold;
}

.contenido {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.33);
  display: flex;
  flex-direction: column;
}

.contenido h3 {
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: bold;
  text-transform: capitalize;
  display: flex;
  justify-content: center;
  /* Centrar horizontalmente */
  align-items: center;
  /* Centrar verticalmente */
}

.contenido p {
  margin-bottom: 5px;
  font-size: 16px;
}

.productos-lista {
  text-decoration: none;
}
.productos-lista li {
  text-decoration: none;
}
.productos-lista img {
  max-width: 300px;
}

.productos-carro {
  margin-left: 10%;
  margin-right: 10%;
}

.header-productos {
  display: flex;
  justify-content: space-between;
}

.contenedor-productos {
  padding-left: 20px;
  padding-right: 20px;
}

.contenedor-admin {
  margin-left: 10%;
  margin-right: 10%;
}

.barra {
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: black;
  cursor: pointer;
}

.barra-servicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-between;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
}

#hero {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  height: 90vh;
  color: black;
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/pesas.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

#hero h1 {
  color: white;
}

#somos_XFit .container {
  text-align: center;
  padding: 200px 12px;
}

#nuestros-servicios {
  background-color: rgb(49, 49, 49);
  color: white;
  text-align: center;
}

#nuestros-servicios .container {
  padding: 150px 12px;
}

#nuestros-servicios h2 {
  margin-top: 0;
}

.carta {
  background-position: center center;
  background-size: cover;
  padding: 250px;
  margin: 70px;
  border-radius: 15px;
}

.carta h3 {
  text-shadow: 0.1em 0.1em 0.1em #0f0f0f;
}

.boxeo {
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../img/boxeo.jpg");
}

.crossfit {
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../img/crossfit.jpg");
}

.zumba {
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../img/zumba.webp");
}

.logo-texto {
  height: 3rem;
}

.formulario {
  width: 95%;
  padding-left: 5%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: none;
  padding: 0.75rem;
  border-radius: 10px;
}

.boton {
  background-color: #329f00;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #226c00;
  cursor: pointer;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: bold;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}